
import React, { Component, Fragment } from 'react'
import { HashRouter as Router, Route } from 'react-router-dom'

import TicketsFilterApp from '/apps/TicketsFilterApp/TicketsFilterApp'
import TicketsApp from '/apps/TicketsApp/TicketsApp'
import BookingModalController from '../BookingApp/controllers/modal/BookingModalController'


class CDMTicketsApp extends Component {



  render() {

    return (
      <Router>

        <Fragment>


          <div className='widget'>
            <TicketsFilterApp
              //noCity
              //noPark
              //noFilter
              //noDate

            />
            <TicketsApp />
          </div>

          <Route
            path='/booking/:park_id/:id/:selectedDate?'
            component={(({ match }) => {
              return <BookingModalController {...match.params} />
            })}
          />
        </Fragment>
      </Router>
    )
  }
}

export default CDMTicketsApp
